import BScroll from 'better-scroll'
export default {
  data () {
    return {
      serviceScroll: null,
      show: 0
    }
  },
  computed: {
    openid () {
      return this.$store.state.user.openid
    }
  },
  mounted () {
    this.init()
    this.getStatus()
  },
  methods: {
    init () {
      this.$nextTick(() => {
        this.serviceScroll = new BScroll(this.$refs.serviceInfo, {
          click: true
        })
      })
    },
    gotoPage (name) {
      this.$router.push({name: name})
    }
  }
}



